.contacts {
  padding-left: 120px;

  @include vp-1199 {
    padding-left: 0;
    padding-right: 133px;
  }

  h2 {
    margin-bottom: 43px;

    @include vp-1199 {
      margin-bottom: 50px;
    }
  }
}

.contacts__list {
  column-count: 2;
  column-gap: 120px;
  column-rule: 1px solid $color-light-slate-grey;

  @include vp-1199 {
    column-gap: 19%;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    gap: 35px;
  }
}

.contacts__item {
  break-inside: avoid;
  display: flex;
  flex-direction: column;
  gap: 15px;

  &:nth-child(odd) {
    margin-bottom: 30px;

    @include vp-1199 {
      margin-bottom: 35px;
      gap: 20px;
    }

    @include vp-767 {
      margin-bottom: 0;
    }
  }

  dt {
    font-weight: 700;
    line-height: 1;

    @include vp-1199 {
      font-size: 20px;
    }
  }

  dd {
    margin: 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 1;

    @include vp-1199 {
      font-size: 18px;
    }
  }

  address {
    font-style: normal;
  }

  a {
    color: inherit;
    transition: opacity $trans-default;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover {
        opacity: 0.7;
      }

      &:focus-visible {
        outline: 1px solid $color-sapphire;
        opacity: 0.7;
      }

      &:active {
        opacity: 0.4;
      }
    }
  }
}
